$border: #C6C6C6;
$gray: #8A999C;
$gray-text: #706F6F;
$gray-bg: #F5F5F5;
$green: #93C01F;
$yellow: #FFC944;
$red: #E6332A;
$blue: #0091D6;
.col-xs-5th,
.col-sm-5th,
.col-md-5th,
.col-lg-5th {
    position: relative;
    min-height: 1px;
    padding-right: 10px;
    padding-left: 10px;
    width: 20%;
    float: left; }
.col-5-th {
	-webkit-box-flex: 0;
	-webkit-flex: 0 0 20%;
	-ms-flex: 0 0 20%;
	flex: 0 0 20%;
	max-width: 20%; }
@media (min-width: 768px) {
	.col-sm-5th {
		-webkit-box-flex: 0;
		-webkit-flex: 0 0 20%;
		-ms-flex: 0 0 20%;
		flex: 0 0 20%;
		max-width: 20%; } }
@media (min-width: 992px) {
	.col-md-5th {
		-webkit-box-flex: 0;
		-webkit-flex: 0 0 20%;
		-ms-flex: 0 0 20%;
		flex: 0 0 20%;
		max-width: 20%; } }
@media (min-width: 1200px) {
	.col-lg-5th {
		-webkit-box-flex: 0;
		-webkit-flex: 0 0 20%;
		-ms-flex: 0 0 20%;
		flex: 0 0 20%;
		max-width: 20%; } }
